.gpt3__possibility {
	display: flex;
	flex-direction: row;
}

.gpt3__possibility-image {
	flex: 1;
	display: flex;
	justify-content: flex-start;
	align-items: center;
}

.gpt3__possibility-image img {
	width: 100%;
	height: 100%;
	object-fit: cover;
}

.gpt3__possibility-content {
	flex: 1;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-start;
}

.gpt3__possibility-content h4 {
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 16px;
	line-height: 30px;
	/* identical to box height, or 188% */
	color: var(--color-h4);
	margin-bottom: 10px;
}

.gpt3__possibility-content h1 {
	font-family: var(--font-family);
	font-weight: 800;
	font-size: 34px;
	line-height: 45px;
	margin-bottom: 23px;
}

.gpt3__possibility-content h4:last-child {
	font-family: var(--font-family);
	font-weight: 500;
	font-size: 16px;
	line-height: 30px;
	/* identical to box height, or 188% */
	color: var(--color-subtext);
	margin-bottom: 30px;
}

.gpt3__possibility-content p {
	font-family: var(--font-family);
	font-weight: 400;
	font-size: 16px;
	line-height: 30px;
	/* or 188% */
	color: var(--color-text);
	margin-bottom: 31px;
}

@media screen and (max-width: 950px) {
	.gpt3__possibility {
		flex-direction: column;
	}
	.gpt3__possibility-image {
		margin: 1rem 0;
	}
	.gpt3__possibility-content {
		margin-top: 2rem;
	}
}
